import { Injectable } from '@angular/core';

import { Observable, of } from 'rxjs';
import { first, map, tap } from 'rxjs/operators';

import { CompanyEarning, Earning, EarningType } from '@app/model';
import { CompanyEarningService, EarningService } from '@app/store/entity';

@Injectable({ providedIn: 'root' })
export class EarningsEditorService {
  /** Company Earnings */
  companyEarnings: CompanyEarning[];
  /** Earnings Metadata */
  earnings: Earning[];
  /** Catalog */
  catalog: EarningType[];

  private currentEarningId: string = null;

  constructor(
      private earningService: EarningService,
      private companyEarningService: CompanyEarningService
      ) {  }

  /**
   * Return boolean observable when the
   * deductions master/metadata have been loaded
   * and service is listening for current company deductions
   * (which are cached in this service).
   * Always emits true when ready.
   */
  ready(): Observable<boolean> {
    if (this.earnings) {
      return of(true);
    }

    return this.earningService.earnings$.pipe(
      first(),
      tap(earnings => {
        this.earnings = earnings.filter(p => !(p.earningTypeName === 'Tips' || p.earningTypeName === 'Clergy'));

        // No need to unsubscribe because this service never dies
        this.companyEarningService.entities$.subscribe(cerns => {
          this.companyEarnings = cerns.filter(c => c.isRemoved !== true);
          // get earnings catalog
          this.updateCategories(this.earnings, this.companyEarnings);
        });
      }),
      map(() => true)
    );
  }

    /**
   * Find or create the CompanyDeductionItem for the give Deduction
   * @param deduction Deduction master metadata for this item
   */
  edit(earning: Earning): CompanyEarning {
    const found = this.companyEarnings.find(
      item => item.earningCode === earning.code);
    return found || this.create(earning);
  }

  create(earningType: Earning): CompanyEarning {
    const item: CompanyEarning = this.companyEarningService.createFromEarning(earningType);
    this.companyEarnings.push(item);
    return item;
  }

  /**
   * Return the terminating, multi-entity-save Observable of the new/changed/deleted CompanyDeductions.
   * Must subscribe to execute save; no need to unsubscribe.
   * Cleans cached CompanyDeductionItems immediately, whether or not subscribed.
   */
  save(ern: CompanyEarning): Observable<any> {
    const toSave: CompanyEarning[] = [];
    toSave.push(ern);
    return this.companyEarningService.save(toSave);
  }

  /**
   Soft-Delete the companyEarning
   * @param ern CompanyEarning
   */
  delete(ern: CompanyEarning): Observable<any> {
    ern.isRemoved = true;
    return this.save(ern); // This does nothing because no subscribe(). What should it do?
  }

  /**
   * Update earnings catalog when there is any company earning added or deleted
   */
  updateCategories(earnings: Earning[], companyEarnings: CompanyEarning[]): void {
    const earningsType = companyEarnings.map(ce => earnings.find(e => e.code === ce.earningCode).earningTypeId);

    const earningsTypeCount = earningsType.reduce((acc, current) => {
      return acc.find(ce => ce.earningTypeId === current)
        ? acc.map(a => a.earningTypeId === current ? { ...a, count: a.count + 1 } : a)
        : acc.concat({ earningTypeId: current, count: 1 });
    }, []);

    const eTypes = earnings.reduce((acc, current) => {
      const { earningTypeId, earningTypeName } = current;
      return acc.find(e => e.earningTypeId === earningTypeId)
        ? acc.map(a => a.earningTypeId === earningTypeId ? { ...a, capacity: a.capacity + 1 } : a)
        : acc.concat({ earningTypeId: earningTypeId, earningTypeName: earningTypeName, capacity: 1 });
    }, []);

    this.catalog = eTypes.filter(p => p.earningTypeId !== null && p.earningTypeId !== 0)
      .map(e => {
        const found = earningsTypeCount.find(et => et.earningTypeId === e.earningTypeId);
        const isActive = found ? found.count < e.capacity : true;
        return {
          earningTypeId: e.earningTypeId,
          earningTypeName: e.earningTypeName,
          isActive: isActive
        };
      }).sort((a, b) => a.earningTypeName.toLowerCase().localeCompare(b.earningTypeName.toLowerCase()));
  }
  /**
   * Sets the current Company Earning Edit to the passed in Id to indicate the company earning under edit
   * @param companyEarningId
   */

  setCurrentEarning(companyEarningId: string): void {
    this.currentEarningId = companyEarningId;
  }
  /**
   * Checks whether the passed in company earning id is under edit
   * @param companyEarningId
   */
  isCurrentEarning(companyEarningId: string): boolean {
    return (this.currentEarningId === companyEarningId);
  }

  /**
   * This method to clear the current earning
   */
  clearCurrentEarning(): void {
    this.currentEarningId = null;
  }

  /**
   * Return distinct tax types given a list of earnings
   * @param earnings Earnings array
   *
   */
  getDistinctTaxTypes(earnings: Earning[]) {
    return earnings.reduce((acc, current) => {
      const { earningTaxType } = current;
      return acc.find(e => e.earningTaxType === earningTaxType) ? acc : acc.concat({ earningTaxType });
    }, []);
  }
}
