import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PageLeaveGuard } from '@core/routing-guards';
import { SharedModule } from '@shared/shared.module';

import { DefaultEarningsListComponent } from '@app/earnings/earnings-default-list.component';
import { EarningsContainerComponent } from '@app/earnings/earnings-container.component';
import { EarningsCatalogComponent } from '@app/earnings/earnings-catalog.component';
import { EarningsGeneralEditorComponent } from '@app/earnings/earnings-general-editor.component';
import { EarningsGuard } from '@app/earnings/earnings-guard';
import { EarningsTableComponent } from '@app/earnings/earnings-table.component';

const routes: Routes = [
  {
    // Expected full path: ~/employees/earnings
    path: '',
    component: EarningsContainerComponent,
    canActivateChild: [EarningsGuard],
    canDeactivate: [PageLeaveGuard],
    data: { title: 'Earnings' },
    children: [
      {
        path: '',
        component: EarningsTableComponent,
        canDeactivate: [PageLeaveGuard],
        data: { title: 'Company Earnings' },
      },
      {
        path: 'general/:id/:code',  // sample: earnings/Meals/23
        component: EarningsGeneralEditorComponent,
        canDeactivate: [PageLeaveGuard],
        data: { title: 'General Editor' },
      },
      {
        path: 'general/:id',
        component: EarningsGeneralEditorComponent,
        canDeactivate: [PageLeaveGuard],
        data: { title: 'General Editor' },
      },
    ]
  }
];

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(routes)
  ],
  exports: [],
  declarations: [
    DefaultEarningsListComponent,
    EarningsContainerComponent,
    EarningsCatalogComponent,
    EarningsTableComponent,
    EarningsGeneralEditorComponent
  ]
})
export class EarningsModule {}
