import { Component, OnInit } from '@angular/core';

import { Observable } from 'rxjs';
import { map, first } from 'rxjs/operators';

import { Earning, EarningsCategory } from '@app/model';
import { EarningService } from '@app/store/entity';

@Component({
  selector: 'sp-default-earnings-list',
  templateUrl: './earnings-default-list.component.html',
})

export class DefaultEarningsListComponent implements OnInit {

  // Default Earnings.
  vm$: Observable<Earning[]>;

  constructor(
    private earningService: EarningService
  ) { }

  ngOnInit() {
    this.vm$ = this.earningService.earnings$.pipe(first(),
        map(earnings => {
          return earnings.filter(e => e.earningsCategory.toUpperCase() === EarningsCategory.DEFAULTOTHERCOMPENSATION);
        }
        ));
      }
}
