import { Injectable } from '@angular/core';

import { ChangeSet } from 'ngrx-data';
import { Observable } from 'rxjs';

import { changeItemFactory as cif } from '@utils';
import { CompanyEarning, Earning } from '@model';
import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';
import { environment } from '@environment';

const ENTITY_COMPANY_EARNING = 'CompanyEarning';

@Injectable({providedIn: 'root'})
export class CompanyEarningService extends CurrentCompanyEntityCollectionService<CompanyEarning> {
  constructor(protected helper: CompanyEntityHelperService) {
    super(ENTITY_COMPANY_EARNING, helper);
  }
  /**
   * Company Earnings
   */
  readonly companyEarnings$ = this.entities$;

  /**
   * Create a new CompanyEarning from a earning master.
   * The id is empty because will save with upsert.
   */
  createFromEarning(earning: Earning): CompanyEarning {
    if (!earning) {
      throw new Error('earning is null');
    }
    return this.create(new CompanyEarning(earning));
  }

  /**
   * Multi-entity save of CompanyEarning with adds, updates and deletes
   * @param companyEarning Array of earnings to add, update, or delete.
   * Updates are items with an id, to delete set isRemoved to true
   * Adds are items without an id; will generate that id here.
   * @returns Terminating observable of ChangeSet that emits when server responds
   */
  save(earnings: CompanyEarning[]): Observable<ChangeSet> {
    // Cannot use ChangeSetOp.Upsert because server does not support it.
    // Separate non-deletes into updates (have id) and adds (no id)
    const updates = earnings.filter(d => d.id);

    // Identity new earnings by missing id and generate their ids before adding
    const adds = earnings
    .filter(d => d.isRemoved !== true && !d.id)
    .map(d => {
      d.id = this.helper.generateId();
      return d;
    });

    const changes = [
      cif.add(ENTITY_COMPANY_EARNING, adds),
      cif.update(ENTITY_COMPANY_EARNING, updates)
    ];
    const changeSet: ChangeSet<CompanyEarning> = { changes };

    return this.saveEntities(changeSet, environment.apiRoot + '/save/companyearning');
  }
}
