import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router} from '@angular/router';

import { CompanyEarning, Earning } from '@app/model';
import { EarningsEditorService } from './earnings-editor.service';

interface EarningsTableViewModel {
  companyEarning: CompanyEarning;
  earningTypeName: string;
  earningTypeId: number;
}
const generalEditorLink = 'general/';

@Component({
  selector: 'sp-earnings-table',
  templateUrl: './earnings-table.component.html',
})
export class EarningsTableComponent implements OnInit {
  @Input() showHeader = true;
  vm: EarningsTableViewModel[] = [];

  constructor(private router: Router,
    private route: ActivatedRoute,
    private earningsEditorService: EarningsEditorService) {
  }

  ngOnInit() {
    this.vm = this.earningsEditorService.companyEarnings
      .filter(ce => ce.id && !(this.earningsEditorService.isCurrentEarning(ce.id)))
      .map(ce => {
        const ern = this.earningsEditorService.earnings.find(e => e.code === ce.earningCode) || {} as Earning;
        return {
          companyEarning: ce,
          earningTypeName: ern.earningTypeName,
          earningTypeId: ern.earningTypeId
        };
      })
      .sort(this.sortEarnings);
  }

  edit(e: EarningsTableViewModel) {
    if (e && e.companyEarning) {
      const id = e.companyEarning.id;
      if (e.earningTypeName) {
        if (id) {
          this.router.navigate([generalEditorLink + e.earningTypeName + '/' + id], { relativeTo: this.route });
        } else {
          this.router.navigate([generalEditorLink + e.earningTypeName], { relativeTo: this.route });
        }
      }
    }
  }

  delete(e: EarningsTableViewModel) {
    if (e) {
      this.earningsEditorService.delete(e.companyEarning).subscribe(
        _ => {
          this.vm = this.vm.filter(m => m !== e);
        }
      );
    }
  }

  private sortEarnings(a: EarningsTableViewModel, b: EarningsTableViewModel): number {
    const typeRes = a.earningTypeName.toLowerCase().localeCompare(b.earningTypeName.toLowerCase());
    const nameRes = a.companyEarning.name.toLowerCase().localeCompare(b.companyEarning.name.toLowerCase());
    const desRes = a.companyEarning.description.toLowerCase().localeCompare(b.companyEarning.description.toLowerCase());

    return typeRes === 0 ? desRes === 0 ? nameRes : desRes : typeRes;
  }
}
