import { Component, Input, Output, EventEmitter } from '@angular/core';
import { EarningType } from '@model';

@Component({
  selector: 'sp-earnings-catalog',
  templateUrl: './earnings-catalog.component.html'
})
export class EarningsCatalogComponent {
  @Input()
  catalog: EarningType[];

  @Output() earningTypeSelected = new EventEmitter<EarningType>();
  @Output() overviewSelected = new EventEmitter();

  catalogTitle = 'Additional Earnings';

  selectType(et: EarningType): void {
    this.earningTypeSelected.emit(et);
  }
  gotoOverview() {
    this.overviewSelected.emit();
  }
}
