import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SafeHtml } from '@angular/platform-browser';

import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

import { CompanyEarning, Earning } from '@app/model';
import { EarningsEditorService } from './earnings-editor.service';
import { LoggerService, WellKnownRoutes } from '@core';

interface EarningsTaxType {
  code: string;
  taxType: string;
  taxTypeUsed: number;
  taxTypeMax: number;
  completed: boolean;
}
/** Private VM class with default values */
class EarningsGeneralEditorModel {
  title = '';
  helpNote: SafeHtml | null = null;
  description = 'Name';
  name = 'ABBR';
  taxTypes: EarningsTaxType[] = [];
  earningCode = '0';
  hideTaxOptions = false;
  selectedTaxType = '';
}

const earningsUrl = WellKnownRoutes.Employees + '/earnings';

@Component({
  selector: 'sp-earnings-general-editor',
  templateUrl: './earnings-general-editor.component.html'
})
export class EarningsGeneralEditorComponent implements OnInit {
  private cancelSave = false;
  private currentEarningItem: CompanyEarning;
  private companyEarningId = '';
  companyEarnings: CompanyEarning[];
  earnings: Earning[];
  isEditMode = false;

  vm = new EarningsGeneralEditorModel();

  constructor(
    private earningsEditorService: EarningsEditorService,
    private logger: LoggerService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    const earnType = this.route.snapshot.params['id'];
    this.companyEarningId = this.route.snapshot.params['code'];
    this.vm.title = earnType;

    this.earnings = this.earningsEditorService.earnings;
    this.companyEarnings = this.earningsEditorService.companyEarnings;

    if (this.companyEarningId) {
      this.isEditMode = true;
      this.configureEditorForEditMode(this.companyEarningId);
    } else {
      this.configureEditorForAddMode(this.vm.title);
    }
  }

  earningSelected(ett: EarningsTaxType): void {
    if (this.isEditMode) {
      this.configureEditorForEditMode(this.companyEarningId, ett.code);
    } else {
      this.configureEditorForAddMode(this.vm.title, ett.code);
    }
  }

  private configureEditorForEditMode(id: string, codeSelected: string = null) {
    // Get the Company Earning being edited
    this.currentEarningItem = this.companyEarnings.find(
      ce => (ce.id || '').toLowerCase() === id.toLowerCase()
    );
    if (!this.currentEarningItem) {
      this.logErrorAndReturn(`Could not find the Company Earning By Id ${id}`);
      return;
    }
    // Get the earnings information for the given company earning.
    let earningTypeSelected: Earning;
    if (codeSelected === null) {
      earningTypeSelected = this.earnings.find(e => e.code === this.currentEarningItem.earningCode);
    } else {
      earningTypeSelected = this.earnings.find(e => e.code === codeSelected);
    }
    // Making sure we have an earningTypeSelected
    if (!earningTypeSelected) {
      this.logErrorAndReturn(`No earning type found for earning by earning
      code : "${this.currentEarningItem.earningCode}" or Code selected: "${codeSelected}" `);
      return;
    }
    // Set the ViewModel with the earningtype selected
    this.setViewModel(earningTypeSelected);
    // To get the tax types available for this earning type, we need the earnings with this tax type and selected tax type.
    const earningsAvailable = this.earnings.filter(
      e => e.earningTypeId === earningTypeSelected.earningTypeId
    );
    let selectedTaxType = '';
    if (codeSelected === null) {
      selectedTaxType = earningTypeSelected.earningTaxType;
    } else {
      selectedTaxType =
        (this.earnings.find(e => e.code === this.currentEarningItem.earningCode) || ({} as Earning))
          .earningTaxType || earningTypeSelected.earningTaxType;
    }
    // get the tax types needed for the view model
    this.vm.taxTypes = this.getTaxTypes(
      earningsAvailable,
      this.currentEarningItem.earningCode,
      selectedTaxType,
      true
    );
    this.earningsEditorService.setCurrentEarning(id);
    // Hide Tax Options when less than one item is available
    this.vm.hideTaxOptions =
      this.vm.taxTypes.filter(taxTypes => taxTypes.completed === false).length <= 1;
  }

  private configureEditorForAddMode(title: string, codeSelected: string = null) {
    const earningsByEarningType = this.earnings.filter(
      e => (e.earningTypeName || '').toLowerCase() === title.toLowerCase()
    );
    const earningCodes = this.companyEarnings
      .filter(ce => ce.id !== null)
      .map(ce => ce.earningCode);
    const ernsAvailable = earningsByEarningType.filter(e => !earningCodes.includes(e.code));

    let earningTypeSelected: Earning;

    if (codeSelected === null) {
      // Get the first available earning
      if (ernsAvailable && ernsAvailable.length > 0) {
        earningTypeSelected = ernsAvailable[0];
      }
    } else {
      // Get the earning by codeSelected
      earningTypeSelected = ernsAvailable.find(e => e.code === codeSelected);
    }
    if (!earningTypeSelected) {
      this.logErrorAndReturn(
        `No earning type found for earning by : "${title}" or "${codeSelected}" `
      );
      return;
    }
    this.currentEarningItem = this.earningsEditorService.edit(earningTypeSelected);
    this.setViewModel(earningTypeSelected);
    this.vm.taxTypes = this.getTaxTypes(
      earningsByEarningType,
      this.vm.earningCode,
      this.vm.selectedTaxType,
      false
    );
    this.vm.hideTaxOptions =
      this.vm.taxTypes.filter(taxTypes => taxTypes.completed === false).length <= 1;
  }

  private setViewModel(earningTypeSelected: Earning) {
    this.vm.earningCode = earningTypeSelected.code;
    this.vm.selectedTaxType = earningTypeSelected.earningTaxType;
    this.vm.description = this.currentEarningItem.description;
    this.vm.helpNote = earningTypeSelected.shortDescription;
    this.vm.name = this.currentEarningItem.name;
  }

  /**
   * Returns an array of taxTypes available with max allowed values and current used value
   * @param earnings Earnings List
   * @param selectedCode SelectedCode for this editor
   * @param selectedTaxType Selected TaxType for this editor
   */
  private getTaxTypes(
    earnings: Earning[],
    selectedCode: string,
    selectedTaxType: string,
    isEditMode: boolean
  ): EarningsTaxType[] {
    const companyCodes = this.companyEarnings
      .filter(ce => ce.id !== null)
      .map(ce => ce.earningCode);

    const distinctTaxTypes = this.earningsEditorService.getDistinctTaxTypes(earnings);

    const taxTypes: EarningsTaxType[] = distinctTaxTypes.map(taxType => {
      let currentSelectedCode = '';
      const currentTaxTypes = earnings.filter(cp => cp.earningTaxType === taxType.earningTaxType);
      const taxTypesNotInCompany = currentTaxTypes.filter(p => !companyCodes.includes(p.code));
      const maxTaxTypeCount = currentTaxTypes.length;
      let currentTaxTypeCount = maxTaxTypeCount - taxTypesNotInCompany.length;
      let hideTaxType = currentTaxTypeCount === maxTaxTypeCount;

      if (selectedTaxType === taxType.earningTaxType) {
        currentSelectedCode = selectedCode;
        if (isEditMode) {
          hideTaxType = false;
        } else {
          currentTaxTypeCount++;
        }
      } else {
        currentSelectedCode =
          taxTypesNotInCompany.length > 0 ? taxTypesNotInCompany[0].code : currentTaxTypes[0].code;
      }

      return {
        code: currentSelectedCode,
        taxType: taxType.earningTaxType,
        taxTypeMax: maxTaxTypeCount,
        taxTypeUsed: currentTaxTypeCount,
        completed: hideTaxType
      };
    });

    return taxTypes;
  }

  getTaxTypesToDisplay(): EarningsTaxType[] {
    return this.vm.taxTypes.filter(tax => tax.completed !== true);
  }

  canLeaveAfterSave(): Observable<any> {
    if (this.cancelSave) {
      this.cancelSave = false;
      return of(true);
    }
    return this.save().pipe(map(e => true));
  }

  save(): Observable<any> {
    this.earningsEditorService.clearCurrentEarning();

    if (this.currentEarningItem) {
      // compare vm with cached earning item, only save if changed
      // since only name and description are editable, it is enough to just compare those two properties
      // also include newly added earning with id of null
      const isChanged =
        this.currentEarningItem.name !== this.vm.name ||
        this.currentEarningItem.description !== this.vm.description ||
        this.currentEarningItem.earningCode !== this.vm.earningCode ||
        !this.currentEarningItem.id;

      if (isChanged) {
        this.currentEarningItem.name = this.vm.name.trim();
        this.currentEarningItem.description = this.vm.description.trim();
        this.currentEarningItem.earningCode = this.vm.earningCode.trim();
        return this.earningsEditorService.save(this.currentEarningItem);
      }
    }
    return of(true);
  }

  done(): void {
    this.router.navigate([earningsUrl]);
  }
  cancel(): void {
    this.earningsEditorService.clearCurrentEarning();
    this.cancelSave = true;
    this.router.navigate([WellKnownRoutes.Earnings]);
  }

  private logErrorAndReturn(message: string) {
    this.logger.error(message);
    this.router.navigate([WellKnownRoutes.Earnings]);
  }
}
