import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Observable, of } from 'rxjs';
import { concatMap, first, mapTo } from 'rxjs/operators';

import { changeItemFactory as cif } from '@utils';
import { CompanyService } from '@entity';
import { EarningsEditorService } from './earnings-editor.service';
import { EarningType, Employee } from '@model';
import { environment } from '@environment';
import { SessionService } from '@session';
import { WellKnownRoutes } from '@core';

/** return route to a view within the Employees area */
function getRoute(ending: string) {
  return WellKnownRoutes.Employees + '/' + ending;
}

interface EarningViewModel {
  employee?: Employee;
  showEarningsCards: boolean;
}

@Component({
  selector: 'sp-earnings',
  templateUrl: './earnings-container.component.html'
})

export class EarningsContainerComponent implements OnInit {
  vm: EarningViewModel;

  constructor(
    private companyService: CompanyService,
    private earningsEditorService: EarningsEditorService,
    private router: Router,
    private sessionService: SessionService,
  ) { }

  ngOnInit() {
    const showEarningsCards = !this.router.url.endsWith('/earnings');

    this.vm = { showEarningsCards };
    this.sessionService.currentEmployee$.pipe(first()).subscribe(ee => this.vm.employee = ee);
  }

  onCatalogSelection(et: EarningType): void {
    if (et && et.earningTypeName) {
      this.router.navigate([getRoute('earnings/general/' + et.earningTypeName)]);
    }
  }

  onOverviewSelected() {
    this.router.navigate([getRoute('earnings')]);
  }

  onReturnEmployeeSelected() {
    if (this.vm.employee) {
      this.router.navigate([`${WellKnownRoutes.Employees}/${this.vm.employee.id}/payment`]);
    }
  }

  canLeaveAfterSave(): Observable<boolean> {
    return this.save();
  }
  getCatalog(): EarningType[] {
    return this.earningsEditorService.catalog;
  }

  // Save company.showEarningsTab:true if have CompanyEarnings and company.showEarningsTab is false.
  save(): Observable<boolean> {
    return this.earningsEditorService.companyEarnings.length === 0
      ? of(true) // no earnings => no save
      : this.companyService.currentCompany$.pipe(
        first(),
        concatMap(cachedCompany => {
          if (cachedCompany.showEarningsTab) {
            return of(true); // already true => no save
          } else {
            // save company.showEarningsTab:true
            const chgCompany = { ...cachedCompany, showEarningsTab: true };
            const changes = [cif.update('Company', chgCompany)];
            const saveUrl = environment.apiRoot + '/save/company';
            return this.companyService.saveEntities({ changes }, saveUrl).pipe(mapTo(true));
          }
        })
      );
  }
}
